<template>
  <div class="hello">
    <!--  nama 是样式前缀   -->
    <transition name="test">
      <div v-if="flag">
        
        <img v-bind:src="data1[msg]">
      </div>
    </transition>
    
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 0,
      data1:[
        './static/y1.jpg',
        './static/y2.jpg',
        './static/y3.jpg',
        './static/y4.jpg',
        './static/y5.jpg',
        './static/y6.jpg',
      ],
      flag:true
    }
  },
  created(){
    // this.flag = !this.flag;
    setInterval(()=>{
      this.msg = Math.trunc(Math.random()*6);
      this.flag = !this.flag;
    },2000);
  },
  watch:{
    msg(){

    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
img{
  width: 500px;
}
a {
  color: #42b983;
}

/* 开始的时候要执行的过度 */
.test-enter-active {
    transition: opacity 3s ;
    transform: rotate() 5s;
}
/* 离开的时候要执行的过度 */
.test-leave-active {
  transition: opacity 3s;

}
/*  开始时候的状态 */
.test-enter{
  opacity: 0.3;
}
/* 离开后的状态 */
.test-leave-to {
  transform: rotate(180deg);
  opacity: 0.3;
}

</style>



